
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import dayjs from "dayjs";
export default {
data() {
return {
dataObj: {},
ID: 0,
isShow: false
};
},
mounted() {
this.ID = this.$route.params.id;
this.getData();
this.$bus.on("refrech", () => {
this.getData();
});
},
beforeDestroy() {
this.$bus.off("refrech");
},
methods: {
getData() {
this.getAxios(`/Customer/GetCustomerLinkManByID/${this.ID}`).then(res => {
this.dataObj = res.Data;
this.dataObj.LinkManAttach = res.Data.ReceiverPhoto
? [res.Data.ReceiverPhoto]
: [];
});
},
delData() {
let _this = this;
this.$vux.confirm.show({
content: "确定删除该条数据？",
onConfirm() {
_this
.postAxios(`/Customer/DeleteCustomerLinkMan`, [_this.ID])
.then(res => {
_this.$vux.toast.show({
type: "success",
text: "删除成功"
});
_this.$bus.emit("init");
_this.$router.back();
})
.catch(err => {
if (err.ErrorMessage) {
_this.$vux.toast.show({
type: "cancel",
text: err.ErrorMessage
});
} else {
_this.$vux.toast.show({
type: "cancel",
text: "删除失败"
});
}
});
}
});
},
goTo() {
this.$router.push({
path: "/client/clientDetails/addCustUser",
query: {
id: this.ID
}
});
},
goMdtPage() {

plus.webview.open(
`http://www.giftmall.top/Mobile/LoginApi/login?oauth=maidiyun&maidiyunToken=${
this.$store.state.token
}&linkID=[${this.dataObj.ID}]&from=crm`
);
},
getPic(value) {
return `${api50}/api/v1/File/DownLoadPic?filePath=${value}`;
}
},
filters: {
dateFormat(val) {
return val ? dayjs(val).format("YYYY-MM-DD") : "";
},
sexFormat(val) {
if (val == 1) {
return "男";
} else if (val == 2) {
return "女";
} else {
return "";
}
}
},
components: {
XHeader,
XButton
}
};



